Office.onReady((info) => {
  console.log(`%c Flex365x Outlook Addin: ${getBuildDateString()} A`, "background: #555; color: #fff; padding: 3px;");
  console.log("taskpane ready");
  Office.context.mailbox.addHandlerAsync(Office.EventType.ItemChanged, itemChanged);
  updateReadUI();
});

const getBuildDateString = () => {
  const date = new Date(111111);

  const months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

  //const mins: string = (date.getMinutes() < 10 ? '0' : '') + date.getMinutes().toString();

  return (
    months[date.getMonth()] +
    " " +
    date.getDate().toString() +
    " " +
    date.getFullYear().toString() +
    " " +
    (date.getHours() < 10 ? "0" : "") +
    date.getHours().toString() +
    ":" +
    (date.getMinutes() < 10 ? "0" : "") +
    date.getMinutes().toString()
  );
};

const openFlexSite = (searchTerm) => {
  window.open(
    `https://pimagov.sharepoint.com/sites/realproperty/SitePages/CollabHome.aspx?sw=bypass&search=${searchTerm}`
  );
};

const getEMLFile = (item) => {
  let emlContents = "";

  const splitLines = (theString, partSize) => {
    let retVal = "";
    const res = [];

    let idx = 0;
    let part = "";
    do {
      part = theString.substr(idx, partSize);
      if (part !== "") {
        res.push(part);
      }
      idx += partSize;
    } while (part !== "");

    if (res.length > -1) {
      retVal = res.join("\r\n");
    }
    return retVal;
  };

  emlContents += `To: ${item.to}\r\nFrom: ${item.sender}\r\nCC: ${item.cc}\r\nSubject: ${item.subject}\r\nDate: ${item.date}\r\n`;
  emlContents += "Content-Type: multipart/mixed; boundary=--boundary_text_string\r\n";
  emlContents += "\r\n----boundary_text_string\r\nContent-Type: text/html; charset=ISO-8859-1\r\n";
  emlContents += "Content-Transfer-Encoding: quoted-printable\r\n\r\n";

  emlContents += `<html><head></head><body>${item.body}</body></html>`;

  for (const attachment of item.attachments) {
    emlContents += "\r\n\r\n----boundary_text_string\r\n";
    emlContents += `Content-Type: ${attachment.contentType}; name="${attachment.name}"\r\n`;
    emlContents += `Content-Disposition: attachment; filename="${attachment.name}"; size=${attachment.size.toString()}\r\n`;
    emlContents += "Content-Transfer-Encoding: base64\r\n\r\n";
    //emlContents += `${attachment.base64Content}`;
    emlContents += `${splitLines(attachment.base64Content, 76)}`;
  }

  emlContents += "\r\n\r\n----boundary_text_string--";

  return emlContents;
};

const readDragger = (e) => {
  e.dataTransfer.setData(
    "text",
    JSON.stringify({
      to: theMailItem.to,
      sender: theMailItem.sender,
      subject: theMailItem.subject,
      date: theMailItem.date,
      cc: theMailItem.cc,
      body: theMailItem.body,
      numAttachments: theMailItem.attachments.length.toString(),
      filename: `${theMailItem.subject.replace(/[^a-z0-9+]+/gi, "_")}_${new Date().getMilliseconds().toString()}${Math.floor(Math.random() * 1000)}.eml`,
      eml: getEMLFile(theMailItem),
    })
  );
};

const getAttachment = (item, attachmentId) => {
  // return (
  //   new Promise() <
  //   Office.AsyncResult <
  //   Office.AttachmentContent >>
  //     ((resolve, reject) => {
  //       item.getAttachmentContentAsync(attachmentId, null, (content) => {
  //         if (content.status === Office.AsyncResultStatus.Succeeded) {
  //           resolve(content);
  //         } else {
  //           reject(content);
  //         }
  //       });
  //     })
  // );

  return new Promise((resolve, reject) => {
    item.getAttachmentContentAsync(attachmentId, null, (content) => {
      if (content.status === Office.AsyncResultStatus.Succeeded) {
        resolve(content);
      } else {
        reject(content);
      }
    });
  });
};

const updateReadUI = () => {
  const mainBody = document.querySelector("#flex-app-body");
  const prompt = document.querySelector("#flex-select-item-prompt");
  const wait = document.querySelector("#flex-wait");
  const searchLinkToContainer = document.querySelector("#flex-search-to-container");
  const searchLinkSenderName = document.querySelector("#flex-search-link-sender-name");
  const searchLinkSenderAddress = document.querySelector("#flex-search-link-sender-address");

  if (searchLinkToContainer && searchLinkSenderName && searchLinkSenderAddress && mainBody && prompt && wait) {
    mainBody.style.display = "none";
    prompt.style.display = "none";
    wait.style.display = "flex";
    searchLinkToContainer.replaceChildren();
    searchLinkSenderName.replaceChildren();
    searchLinkSenderAddress.replaceChildren();
  } else {
    // can't find our container, we'll bail
    return;
  }

  const theItem = Office.context.mailbox.item;
  const tempItem = {
    to: "",
    sender: "",
    subject: "",
    date: "",
    cc: "",
    body: "",
    attachments: [],
  };

  if (!theItem) {
    wait.style.display = "none";
    prompt.style.display = "flex";
    return;
  }

  tempItem.sender = getEmailString(theItem.sender);
  tempItem.date = `${theItem.dateTimeCreated.toUTCString()}`;
  theItem.to.forEach((address) => {
    tempItem.to += (tempItem.to.length > 0 ? "; " : "") + getEmailString(address);
  });

  theItem.cc.forEach((address) => {
    tempItem.cc += (tempItem.cc.length > 0 ? "; " : "") + getEmailString(address);
  });

  tempItem.subject = theItem.subject;
  // theItem.body.getAsync(Office.CoercionType.Html, null, async (b) => {
  //   theItemRef.current.body = b.value;

  theItem.body.getAsync(Office.CoercionType.Html, null, async (b) => {
    tempItem.body = b.value;

    // tslint:disable

    for (const attachment of theItem.attachments) {
      try {
        const content = await getAttachment(theItem, attachment.id);
        tempItem.attachments.push({
          id: attachment.id,
          name: attachment.name,
          contentType: attachment.contentType,
          size: attachment.size,
          base64Content: content.value.content,
        });

        console.log(content);
      } catch (err) {
        console.log("error caught");
        console.log(err);
      }
    }

    theMailItem = Object.assign({}, tempItem);

    const dragElement = document.querySelector("#flex-drag-envelope");
    if (dragElement) {
      dragElement.addEventListener("dragstart", readDragger);
    }

    // sender
    const senderNameLink = document.createElement("a");
    senderNameLink.href = "javascript:void(0)";
    senderNameLink.innerText = theItem.sender.displayName;
    senderNameLink.addEventListener("click", () => openFlexSite(`${theItem.sender.displayName}`));

    searchLinkSenderName.replaceChildren();
    searchLinkSenderName.append(senderNameLink);

    const senderNameAddress = document.createElement("a");
    senderNameAddress.href = "javascript:void(0)";
    senderNameAddress.innerText = theItem.sender.emailAddress;
    senderNameAddress.addEventListener("click", () => openFlexSite(`${theItem.sender.emailAddress}`));

    searchLinkSenderAddress.replaceChildren();
    searchLinkSenderAddress.append(senderNameAddress);

    //flex-search-to-container

    const searchLinkToContainer = document.querySelector("#flex-search-to-container");
    if (searchLinkToContainer) {
      searchLinkToContainer.replaceChildren();
      theItem.to.forEach((to) => {
        if (to.emailAddress !== theItem.sender.emailAddress) {
          const div = document.createElement("div");

          const linkName = document.createElement("a");
          linkName.href = "javascript:void(0)";
          linkName.innerHTML = `${to.displayName}`;
          linkName.addEventListener("click", () => openFlexSite(`${linkName.innerHTML}`));

          div.appendChild(linkName);
          if (searchLinkToContainer.children.length > 0) {
            div.style.paddingTop = "10px";
          }

          searchLinkToContainer.appendChild(div);

          if (to.displayName !== to.emailAddress) {
            const div2 = document.createElement("div");
            const linkAddress = document.createElement("a");
            linkAddress.href = "javascript:void(0)";
            linkAddress.innerHTML = `${to.emailAddress}`;

            linkAddress.addEventListener("click", () => openFlexSite(`${linkAddress.innerHTML}`));

            div2.appendChild(linkAddress);
            searchLinkToContainer.appendChild(div2);
          }
        }
      });
    }
    wait.style.display = "none";
    prompt.style.display = "none";
    mainBody.style.visibility = "hidden";
    mainBody.style.display = "flex";

    // set height
    const divElement = searchLinkToContainer;
    divElement.style.resize = "";
    divElement.style.overflow = "";
    divElement.style.height = "auto";

    if (divElement.getBoundingClientRect().height > 120) {
      divElement.style.resize = "vertical";
      divElement.style.overflow = "auto";
      divElement.style.height = "120px";
    }

    mainBody.style.visibility = "";
  });
};

const getEmailString = (details) => {
  return `${details.displayName}${details.displayName.indexOf("@") === -1 ? ` (${details.emailAddress})` : ""}`;
};

function itemChanged(e) {
  updateReadUI();
}
